import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewerOnShopify extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Integrate the viewer with your Wix site</h1>
                <h2>Add the viewer to the product template page</h2>
                <h4>1. Add the Wix product id to the Ezyvue product metadata</h4>
                <ol>
                    <li>Navigate to Products on the Dashboard and select a product</li>
                    <img alt='wix products' src={require('assets/images/wix-products.jpg')} />
                    <li>Copy the Wix product id from the browser address bar (text after the last / character) on the product page.</li>
                    <img alt='wix products' src={require('assets/images/wix-product-address-bar.jpg')} />
                    <li>Go the Manage Project page on Ezyvue and paste the value into the External Product ID textbox in the Properties section.</li>
                    <img alt='wix products' src={require('assets/images/wix-external-id.jpg')} />
                    <li>Click Save Changes.</li>
                </ol>
                <h4>2. Add the Wix Velo editor to Wix</h4>
                The Velo editor will be needed to add a snippet of Javascript code in step 4.
                <ol>
                    <li>Log into your Wix website.</li>
                    <li>Go to https://www.wix.com/velo and click the Start a Project button.</li>
                    <li>Select your website and click Start Now.</li>
                    <li>The Velo editor will open.</li>
                    <br/>
                    Note: once this step is completed Velo will be enabled for your Wix site so you don't have to use the link above in the future.
                </ol>
                <h4>3. Add an Embed Code element to the product template page</h4>
                <ol>
                    <li>Edit your site and navigate to the product template page. </li>
                    <img alt='wix products' src={require('assets/images/wix-product-template.jpg')} />
                    <li>Click the Add icon in the editor and select Embed Code. In popular embeds choose Embed a site and click the + button to add to the page.</li>
                    <img alt='wix products' src={require('assets/images/wix-add-embed.jpg')} />
                    <li>Drag the element into a section and note the ids of the control and the strip, these will be needed in the next step.</li>
                    <img alt='wix products' src={require('assets/images/wix-embed-added.jpg')} />
                </ol>
                <h4>4. Copy and paste code</h4>
                <ol>
                    <li>Copy and paste the code below into the Velo code window. If there is already an onReady function, add the contents of the onReady function below to it.</li>
                    <img alt='wix products' src={require('assets/images/wix-add-code.jpg')} />
                    <pre>
                        {`
    $w.onReady(async function () {
        const product = await $w('#productpageid').getProduct();
        const resp = await fetch('https://www.ezyvue.com/product-service/api/product?externalProductID=' + product._id);

        const viewerOptions = '&hidemenu';

        if(resp.ok) {
            const evProduct = await resp.json();
            if(evProduct)
                $w('#htmlid').src='https://www.ezyvue.com/viewer?productId=' + evProduct._id + viewerOptions;
            else
                $w('#sectionid').hide();
        }
        else
            $w('#sectionid').hide(); 
    });
                    `}
                    </pre>
                </ol>
                <h4>5. Modify the code</h4>
                <p>
                        To make the code work on your page the following element ids in the code above need to be changed to the ids on your page:<br /><br />
                        #sectionid - change to the id of the strip where the Embed code element was added (usually #section1, #section2 etc).<br />
                        #htmlid - change to the id of the Embed Code element (usually #html1, #html2 etc).<br />
                        #pageid  - change to the id of the page (usually #productPage).<br /><br />

                        The id of an element can be found in the tab in the top left corner when you move your mouse over it. Also as you type the id the editor will auto-complete. Invalid ids will be shown in red.
                </p>
                <h4>6. Run the code</h4>
                <p>
                    The 3d model should appear on the page.
                </p>
                <h4>7. Set the viewer options</h4>
                Note: product-specific settings can be set in the settings tab of Ezyvue Studio.
                <ol>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>Replace the &amp;hidemenu option in the code above with the options from the HTML code textbox. (Each option begins with the character &amp;).</li>
                </ol>
                <h2>Add the viewer for a specific product</h2>
                <p>
                    You can embed the viewer on any template page on Wix by addding an iframe to an Embed HTML element.<br />
                    Follow this <Link to='/help/embedViewer'>guide</Link>
                </p>
            </div>
        );
    }
}

